#record {
	width: 100%;
	background: #fffcfa;
	padding: 100px 0;

	.inner {
		width: 1180px;
		margin: 0 auto;

		> .wrap {
			> article {
				float: left;

				&:first-child {
					width: 580px;
					margin-right: 20px;

					.experience {
						width: 100%;
						margin-bottom: 40px;
					}
					.license {
						width: 100%;

						.con {
							> .txt {
								margin-bottom: 10px;

								ul {
									display: flex;
									flex-wrap: wrap;
									justify-content: space-between;
									align-items: center;

									li {
										&:first-child {
											width: 60%;
										}
										&:last-child {
											width: 40%;
										}
									}
								}
							}
						}
					}
				}
				&:last-child {
					width: 580px;
				}

				h1 {
					font: bold 24px/1 'Raleway';
					text-transform: uppercase;
					color: #333;
					margin-bottom: 30px;
				}

				.con {
					width: 100%;
					border-left: 1px solid #ffd5ce;
					padding-left: 20px;

					.txt {
						margin-bottom: 40px;
						position: relative;

						&:last-child {
							margin-bottom: 0;
						}

						h2 {
							font: bold 20px/1 'Gowun Dodum';
							color: #ff9a8f;
							margin-bottom: 15px;
							position: relative;

							&::before {
								content: '';
								display: block;
								position: absolute;
								top: 0;
								left: -25px;
								width: 10px;
								height: 10px;
								border-radius: 50%;
								background: #ff9a8f;
							}
						}
						h3 {
							font: normal 14px/1 'Raleway';
							color: #888;
							margin-bottom: 20px;
						}
						p {
							font: 16px/1.5 'Gowun Dodum';
							color: #333;
							margin-bottom: 5px;
						}
					}
				}
			}
		}
	}
}

@media screen and (max-width: $tablet) {
	#record {
		.inner {
			width: 100%;
			padding: 0 30px;

			> .wrap {
				> article {
					&:first-child {
						width: 100%;
						margin-right: 0;

						.license {
							margin-bottom: 40px;
						}
					}
					&:last-child {
						width: 100%;
					}
				}
			}
		}
	}
}
